import styled from "styled-components";

const FooterStyled =  styled.div`
#FooterInfo {
  height: 6vh;
  display: flex; 
  align-items: center;
  justify-content: center;
  .fi_circle{
    width: 3.7rem;
    height:3.7rem;
    border-radius: 50%;
    display: flex; 
    justify-content: center;
    align-items: center;
    background-color: #fff;
    margin-left: 0.8rem
  }
  .fi_aTag {
    font-size: 1.1rem;
    font-weight: 600;
    display: block;
    margin-left: 1.5rem;
    padding-bottom : 0.3rem;
  }
}
`;

const FooterInfo = ({src, alt, href, children})=>{
  return(
    <FooterStyled>
      <div id="FooterInfo" className="fi_outbox">
        <div className="fi_circle">
          <img src={src} alt={alt}></img>
        </div>
        <a className="fi_aTag" href={href} >{children}</a>
      </div>
    </FooterStyled>
  );
};
export default FooterInfo;