import styled from "styled-components";
import data from '../pages/ImageData.json';

const WebOutbox = styled.div`
  .webBox_inner{
    margin: 0 auto;
    box-sizing: border-box;
    background-color: #fff;
    width: 90%;
    height: 410px;
    outline: 1px solid #111;
    
    .webImg{
      width: 100%;
      height: 74%;
      img {
        display: block; width:100%; height: 100%;
      }
    }
    .webText {
      position: relative;
      width: 100%;
      height: 45%;
      background-color: #555;
      padding: 12px 26px;
      h4 {
        color: #fff; font-size: 16px; font-weight: 500;
      }
      p.websub {
        color: #d9d9d9; font-size: 12px; margin-top: 6px;
      }
      .webTag {
        display: flex; flex-wrap: wrap;
      }
      .btn {
        width: 1.2rem; height: 1.2rem;
        display:block;
        position: absolute;
        top:1.2rem ;
        right:1.5rem;
      }
    }
  }

  .tagOutline {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #7CC6CB;
    padding: 3px 12px;
    border-radius: 15px;
    height: 21px;
    margin: 10px 10px 0 0;
    p{
      color: #7CC6CB;
      font-size: 12px;
      margin-bottom: 3px;
    } 
  }
`;

const SmallTag = ({children}) => {
  return(
        <div className="tagOutline">
          <p>{children}</p>
        </div> 
  );
}

const WebBox = ({id})=>{
  const dataJson = data.data.find((item)=>item.id === id );

  return(
    <WebOutbox>
      <div className="webBox_inner">
        <div className="webImg" >
          <img src={dataJson.image} alt={dataJson.name}/>
        </div>
        <div className="webText">
          <div className="webTitle">
            <h4>{dataJson.name}</h4>
            <p className="websub"> {dataJson.sub}</p>
          </div>
          <div className="webTag">
            {dataJson.tag.map((tag, index)=>(
              <SmallTag key={index}>{tag}</SmallTag>
            ))}
          </div>
          <a className="btnLink" href={dataJson.url} target="_blank" rel="btn_link">
            <img className="btn" src={ require('../pages/img/btn.png') } alt="  ,    ."/>
          </a>
        </div> {/* webText end */}
      </div>{/* innerBox end */}
    </WebOutbox> 
  );
};
export default WebBox;