import { useEffect, useRef } from 'react';

const First = ()=>{
  return(
    <div className="first">
      <h4></h4>
      <p className='spam'>YOONJieut</p>
      <p className='up'>Web Publisher Portfolio</p>
      <p className='down'>    </p>
    </div>
  )
}

const Second = ()=>{
  return(
    <div className="second">
        <h4>
          <span> :)</span><br/>
           !
        </h4>
    </div>
  );
}


const Intro = ()=>{

  const videoRef= useRef(null);
  useEffect(() => {
    const setPlayBackRate = () => {
      videoRef.current.playbackRate = 0.7;
    };
    setPlayBackRate();
  }, []);

  return(
    <div id='Intro' className="fullPage">
      <div className='introText'>
        <First/>
        <Second />
        <div className='scrollDown'>
          <img src={require('../pages/img/scrollDown.png')} alt="scrolldown icon"/>
          <p>Scroll Down</p>
        </div>
      </div>
      <div className='bg_vid'>
        <video autoPlay muted loop playsInline
          src='/vid/smog.mp4'
          ref={videoRef}
        />
      </div>
    </div>
  );
}
export default Intro;