import { Link } from "react-scroll";

const Header = ()=>{
  return(
    <header id="header">
      <div className="head_inner">
        <h1><Link to="Intro" smooth={true} duration={700}>YOONJieut</Link></h1>
        <ul className="gnb">
          <li>
            <Link to="about" smooth={true} duration={700}>About</Link>
          </li>
          <span> | </span> 
          <li><Link to="stack" smooth={true} duration={700}>Stack</Link></li>
          <span> | </span>
          <li><Link to="web" smooth={true} duration={700}>Web</Link></li>
          <span> | </span>
          <li><Link to="banner" smooth={true} duration={700}>Banner</Link></li>
          <span> | </span>
          <li><Link to="Footer" smooth={true} duration={700}>Contact</Link></li>
        </ul>
      </div>
    </header>
  );
};
export default Header;