import React, { useState, useEffect } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, Navigation, Pagination, Scrollbar } from 'swiper';
import data from './ImageData.json';
import './Banner.scss';

// swiper style import
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import 'swiper/css/autoplay';
import 'swiper/css/scrollbar';

export default function Banner() {
  const navigationPrevRef = React.useRef(null);
  const navigationNextRef = React.useRef(null);

  const [banner, setBanner] = useState([]);
  useEffect(() => {
    // JSON    state 
    setBanner(data.banner);
  }, []);

  const [currentSlideIndex, setCurrentSlideIndex] = useState(0);

  return (
    <div id='banner' className='fullPage'>
      <div className='banner_inner'>
        <h1>Banner</h1>
        <Swiper
          modules={[Pagination, Navigation, Autoplay, Scrollbar]}
          loop={false}
          centeredSlides={true}
          spaceBetween={20}
          slidesPerView={1}
          scrollbar={{ 
            //  
            draggable: true, 
            el:null
          }}
          pagination={{ clickable: true }}
          navigation={{
            prevEl: navigationPrevRef.current,
            nextEl: navigationNextRef.current,
          }}
          onSlideChange={(swiper) => setCurrentSlideIndex(swiper.realIndex)}
          onInit={(swiper) => {
            swiper.params.navigation.prevEl = navigationPrevRef.current;
            swiper.params.navigation.nextEl = navigationNextRef.current;
            swiper.navigation.init();
            swiper.navigation.update();
          }}
        >
          {banner.map((item, index) => (
            <SwiperSlide key={index}>
              <img src={item.image} alt={item.name} /> 
            </SwiperSlide>
          ))}
          <p className='slideName'>{banner[currentSlideIndex]?.name}</p>
        </Swiper>
        <div className='btnPrev' ref={navigationPrevRef} >
          <img src={require('./img/btnLeft.png')} alt='Prev Button'/>
        </div>
        <div className='btnNext' ref={navigationNextRef} >
          <img src={require('./img/btnRight.png')} alt='Next Button'/>
        </div>
      </div>
    </div>
  );
}